<!--clients--><section class="clients" v-if="clientsData"><div class="container"><div class="content"><carousel class="clients-carousel" :items-to-show="5" :autoplay="4000" :settings="settings" :breakpoints="breakpoints" dir="ltr"><slide class="item" v-for="client in clientsData" :key="client.id"><img class="img-fluid" :src="client.image" :alt="client.image_alt"></slide></carousel></div></div></section><!--clients-->