<!--if profile updated successfully--><Toast :showIfValue="profileUpdated" :message="$t('your_data_updated_successfully')" :success="true"></Toast><!--profile--><section class="profile"><div class="container"><div class="profile-content"><div class="row"><div class="col-lg-3"><div class="top-part"><div class="nav flex-column nav-pills shadow" id="profile_tabs" role="tablist" aria-orientation="vertical"><button class="nav-link active" id="v-pills-1-tab" data-bs-toggle="pill" data-bs-target="#v-pills-1" type="button" role="tab" aria-controls="v-pills-1" aria-selected="false">{{ $t("edit_profile") }}</button><button class="nav-link" id="v-pills-2-tab" data-bs-toggle="pill" data-bs-target="#v-pills-2" type="button" role="tab" aria-controls="v-pills-2" aria-selected="false">{{ $t("change_password") }}</button></div><div class="profile-info shadow"> <div class="image"> <img class="img-fluid" src="@/assets/images/blank.png" alt=""></div><div class="details"> <h3>{{ userData.name }}</h3><router-link class="btn btn-primary" to="/" @click="logout">{{ $t("logout") }}</router-link></div></div></div></div><div class="col-lg-9"><div class="tab-content" id="profile_tabsContent"><div class="tab-pane fade show active" id="v-pills-1" role="tabpanel" aria-labelledby="v-pills-1-tab"> <form class="g-3 shadow" enctype="multipart/form-data" @submit.prevent="handleProfileForm"><div class="row"><div class="col-lg-12"><div class="form-group"><label for="p_name">{{ $t('name') }}</label><input class="form-control" type="text" id="p_name" :placeholder="$t('name')" name="name" v-model="profileFormData.name"><div class="invalid-feedback" v-if="profileErrors.name &amp;&amp; !profileUpdated" v-for="error in profileErrors.name">{{ error }}</div></div></div><div class="col-lg-12"><div class="form-group"><label for="p_phone">{{ $t('phone') }}</label><input class="form-control" type="text" id="p_phone" :placeholder="$t('phone')" name="phone" v-model="profileFormData.phone"><div class="invalid-feedback" v-if="profileErrors.phone &amp;&amp; !profileUpdated" v-for="error in profileErrors.phone">{{ error }}</div></div></div><div class="col-lg-12"><div class="form-group"><label for="p_email">{{ $t('email') }}</label><input class="form-control" type="email" id="p_email" :placeholder="$t('email')" name="email" v-model="profileFormData.email"><div class="invalid-feedback" v-if="profileErrors.email &amp;&amp; !profileUpdated" v-for="error in profileErrors.email">{{ error }}</div></div></div><div class="col-lg-12"><div class="form-group"><button class="btn btn-danger hvr-sweep-to-top" type="submit"> <span v-if="!updating">{{ $t('save_changes') }}</span><span v-if="updating">{{ $t('sending') }}</span><div class="spinner-border text-light" role="status" v-if="updating"><span class="visually-hidden">Loading...</span></div></button></div></div></div></form></div><div class="tab-pane fade" id="v-pills-2" role="tabpanel" aria-labelledby="v-pills-2-tab"> <form class="g-3 shadow" enctype="multipart/form-data" @submit.prevent="handleUpdatePassword"><div class="alert alert-danger d-flex align-items-center" role="alert" v-if="passwordError"><div>{{ passwordError }}</div></div><div class="row"><div class="col-lg-12"><div class="form-group"><label for="old_password">{{ $t('old_password') }}</label><input class="form-control" type="password" name="old_password" id="old_password" v-model="passwordFormData.old_password"><div class="invalid-feedback" v-if="passwordErrors.old_password" v-for="error in passwordErrors.old_password">{{ error }}</div></div></div><div class="col-lg-6"><div class="form-group"><label for="new_password">{{ $t('new_password') }}</label><input class="form-control" type="password" name="new_password" id="new_password" v-model="passwordFormData.new_password"><div class="invalid-feedback" v-if="passwordErrors.new_password" v-for="error in passwordErrors.new_password">{{ error }}</div></div></div><div class="col-lg-6"><div class="form-group"><label for="confirm_password">{{ $t('confirm_password') }}</label><input class="form-control" type="password" name="confirm_password" id="confirm_password" v-model="passwordFormData.confirm_password"><div class="invalid-feedback" v-if="passwordErrors.confirm_password" v-for="error in passwordErrors.confirm_password">{{ error }}</div></div></div><div class="col-lg-12"><div class="form-group"><button class="btn btn-danger hvr-sweep-to-top" type="submit"> <span v-if="!updating">{{ $t('save_changes') }}</span><span v-if="updating">{{ $t('sending') }}</span><div class="spinner-border text-light" role="status" v-if="updating"><span class="visually-hidden">Loading...</span></div></button></div></div></div></form></div></div></div></div></div></div></section><!--profile -->