<div class="product-box"><div class="image"><router-link :to="`/products/${product.id}`"> <img class="img-fluid" :src="product.image" :alt="product.image_alt"></router-link><div class="fav" v-if="!userDataCheck"><router-link class="btn" to="/account"><i class="bi bi-heart"></i></router-link></div><div class="fav" v-if="userDataCheck"><form enctype="multipart/form-data" @submit.prevent="updateWishlist"><input name="product_id" :value="product.id" type="hidden"><button class="btn" type="submit"> <i class="bi bi-heart-fill" v-if="userWishlist.some(wish =&gt; wish.id == product.id)"></i><i class="bi bi-heart" v-else></i></button></form></div><div class="layer" v-if="userDataCheck &amp;&amp; productTotal(product) &lt;= 0"><a class="btn" @click="addToCart(product)">{{ $t("add_to_cart") }}</a></div><div class="layer" v-if="userDataCheck &amp;&amp; productTotal(product) &gt; 0"><a class="btn added"> <i class="bi bi-check"></i></a></div><div class="layer" v-if="!userDataCheck"><router-link class="btn" to="/account">{{ $t("add_to_cart") }}</router-link></div></div><div class="details"><div class="title"><h3><router-link :to="`/products/${product.id}`">{{ product.title }}</router-link></h3></div></div></div>