<!--about page--><section class="about-in-page" v-if="aboutApi"><div class="container"><div class="content"> <div class="about-box" v-for="about in aboutApi" :key="about.id"><div class="row"><div class="col-lg-6"><div class="about-image" data-aos="fade-up"><img class="img-fluid" alt="" :src="about.image"></div></div><div class="col-lg-6"><div class="about-text"><div class="main-title"><h2>{{ about.title }}</h2><div class="decore-title"><img src="@/assets/images/favicon.png" alt="favicon"></div></div><div v-html="about.description"></div></div></div></div></div></div></div></section><!--about page-->