<!--if order sent successfully--><div class="position-fixed bottom-0 start-0 p-3 toast-part"><div class="toast align-items-center text-white bg-success border-0" :class="{ 'show': checkout_done }" role="alert" aria-live="assertive" aria-atomic="true"><div class="d-flex"><div class="toast-body">{{ $t("your_order_sent_successfully") }}</div><button class="btn-close btn-close-white me-2 m-auto" type="button" data-bs-dismiss="toast" aria-label="Close"></button></div></div></div><!--checkout page--><section class="checkout"><div class="container"><div class="content"><form enctype="multipart/form-data" @submit.prevent="handlecheckoutForm"><div class="row"><div class="col-lg-9"><div class="checkout-form"><div class="title"><h3>{{ $t("checkout_details") }}</h3></div><div class="row"><div class="col-lg-6"><div class="form-group"><label for="">{{ $t("name") }} *</label><input class="form-control" type="text" name="name" v-model="checkoutFormData.name"><div class="invalid-feedback" v-if="checkoutErrors.name &amp;&amp; !checkout_done" v-for="error in checkoutErrors.name">{{ error }}</div></div></div><div class="col-lg-6"><div class="form-group"><label for="">{{ $t("phone") }} *</label><input class="form-control" type="text" name="phone" v-model="checkoutFormData.phone"><div class="invalid-feedback" v-if="checkoutErrors.phone &amp;&amp; !checkout_done" v-for="error in checkoutErrors.phone">{{ error }}</div></div></div><div class="col-lg-12"><div class="form-group"><label for="">{{ $t("address") }} *</label><textarea class="form-control" rows="3" name="address" v-model="checkoutFormData.address"></textarea><div class="invalid-feedback" v-if="checkoutErrors.address &amp;&amp; !checkout_done" v-for="error in checkoutErrors.address">{{ error }}</div></div></div></div></div></div><div class="col-lg-3"><div class="checkout-total"><table class="table"><thead><tr> <th class="product">{{ $t("product") }}</th><th class="subtotal">{{ $t("subtotal") }}</th></tr></thead><tbody><CheckoutItemSummary v-for="product in cartProducts" :key="product.id" :product="product"></CheckoutItemSummary><tr><td class="total-title">{{ $t("total_price") }}</td><td class="total"> <strong class="me-1">{{ cartTotal.toFixed() }}</strong>{{ $t("currency") }}</td></tr></tbody></table><div class="btns"><button class="btn"> <span v-if="!sending">{{ $t("order_now") }}</span><span v-if="sending">{{ $t("sending") }}</span><div class="spinner-border text-light" role="status" v-if="sending"><span class="visually-hidden">Loading...</span></div></button></div></div></div></div></form></div></div></section>