<!--if mesage sent--><Toast :showIfValue="sent" :message="$t('message_sent_successfully')" :success="true"></Toast><!--if mesage not sent--><Toast :showIfValue="recapchaError" :message="$t('recapcha_error')" :success="false"></Toast><!--contact--><section class="contact"><div class="container"><div class="info-area"><div class="row"><div class="col-md-4 col-lg-4"><div class="info-box" data-aos="fade-up"><i class="bi bi-envelope"> </i><h3>{{ $t("email") }}</h3><a :href="'mailto:'+settingsData.email">{{ settingsData.email }}</a></div></div><div class="col-md-4 col-lg-4">  <div class="info-box" data-aos="fade-up" :data-aos-delay="200"><i class="bi bi-telephone"></i><h3>{{ $t("phone") }}</h3><a :href="'tel:'+settingsData.phone" dir="ltr">{{ settingsData.phone }}</a></div></div><div class="col-md-4 col-lg-4"><div class="info-box" data-aos="fade-up" :data-aos-delay="300"><i class="bi bi-pin-map"></i><h3>{{ $t("address") }}</h3><span>{{ settingsData.address }}</span></div></div></div></div><div class="form-area" data-aos="fade-up" :data-aos-delay="500"><div class="contact-box"><div class="form-content"><form class="g-3" enctype="multipart/form-data" @submit.prevent="handleContactForm"><div class="row"><div class="col-lg-12"><div class="form-group input-group"><input class="form-control" type="text" :placeholder="$t('name')" name="name" v-model="contactFormData.name"><div class="invalid-feedback" v-if="contactErrors.name &amp;&amp; !sent" v-for="error in contactErrors.name">{{ error }}</div></div></div><div class="col-lg-12"><div class="form-group input-group"><input class="form-control" type="text" :placeholder="$t('phone')" name="phone" v-model="contactFormData.phone"><div class="invalid-feedback" v-if="contactErrors.phone &amp;&amp; !sent" v-for="error in contactErrors.phone">{{ error }}</div></div></div><div class="col-lg-12"><div class="form-group input-group"><input class="form-control" type="text" :placeholder="$t('email')" name="email" v-model="contactFormData.email"><div class="invalid-feedback" v-if="contactErrors.email &amp;&amp; !sent" v-for="error in contactErrors.email">{{ error }}</div></div></div><div class="col-lg-12"><div class="form-group input-group"><textarea class="form-control" rows="6" :placeholder="$t('message')" name="message" v-model="contactFormData.message"></textarea><div class="invalid-feedback" v-if="contactErrors.message &amp;&amp; !sent" v-for="error in contactErrors.message">{{ error }}</div></div></div><div class="col-lg-12 mb-3"><vue-recaptcha @verify="onVerify" @expired="onCaptchaExpired" ref="recaptcha" sitekey="6LeMuKkeAAAAAJGnqbyd2jEWdTS65wP5Xwtw9H81"></vue-recaptcha></div><div class="col-lg-12 text-center"><div class="form-group input-group"><button class="btn btn-danger hvr-sweep-to-top" type="submit" :disabled="sending == true"> <span v-if="!sending">{{ $t('send') }}</span><span v-if="sending">{{ $t('sending') }}</span><div class="spinner-border text-light" role="status" v-if="sending"><span class="visually-hidden">Loading...</span></div></button></div></div></div></form></div></div></div></div></section><!--contact --><!--map--><section class="map"><iframe :src="settingsData.map_url" width="100%" height="450" style="border:0;" allowfullscreen="" loading="lazy"></iframe></section><!--map-->