<!--cart--><section class="cart"><div class="container"><div class="cart-content"><div class="notfound-txt" v-if="cartProducts.length &lt; 1"><p class="oops">{{ $t("oops") }}!</p><p class="notfound-text"><strong>{{ $t("no_products_in_cart") }}</strong></p></div><div class="row" v-if="cartProducts.length &gt; 0"><div class="col-lg-9"><div class="table-responsive table-area"><table class="table"><thead><tr><th>{{ $t("product") }}</th><th>{{ $t("price") }}</th><th>{{ $t("quantity") }}</th><th>{{ $t("subtotal") }}</th><th> </th></tr></thead><tbody><CartItem v-for="product in cartProducts" :key="product.id" :product="product"></CartItem></tbody></table></div></div><div class="col-lg-3"><CartSummary></CartSummary></div></div></div></div></section><!--cart-->