<!--if review not sent--><Toast :showIfValue="alreadyReviewed" :message="$t('you_already_reviewed_this_product')" :success="false"></Toast><!--if review sent--><Toast :showIfValue="sent" :message="$t('your_review_sent_successfully')" :success="true"></Toast><!--single product--><section class="single-product"><div class="container"><div class="content"><div class="alert alert-success d-flex justify-content-between" role="alert" v-if="totalProducts"><div class="d-flex align-items-center"><svg class="bi flex-shrink-0 me-2" width="24" height="24" role="img" aria-label="Success:"><use xlink:href="#check-circle-fill"></use><symbol fill="#8fae1b" id="check-circle-fill" viewBox="0 0 16 16"><path d="M16 8A8 8 0 1 1 0 8a8 8 0 0 1 16 0zm-3.97-3.03a.75.75 0 0 0-1.08.022L7.477 9.417 5.384 7.323a.75.75 0 0 0-1.06 1.06L6.97 11.03a.75.75 0 0 0 1.079-.02l3.992-4.99a.75.75 0 0 0-.01-1.05z"></path></symbol></svg><div>{{ totalProducts }} {{ $t("products_added_to_your_cart") }}.</div></div><router-link class="btn" to="/cart"><i class="bi bi-cart-check me-1"></i>{{ $t("view_cart") }}</router-link></div><div class="single-product-details"><div class="row"><div class="col-lg-5 col-md-6"><div class="image"><a :href="product.image" data-fancybox :data-caption="product.title"> <img class="img-fluid" :src="product.image" :alt="product.image_alt"></a></div></div><div class="col-lg-7 col-md-6"><div class="text"><h3>{{ product.title }}</h3><span class="badge" v-if="product.in_stock">{{ $t('in_stock') }}</span><span class="badge" v-if="!product.in_stock">{{ $t('out_of_stock') }}</span><p>{{ product.description }}</p><div class="btns" v-if="userDataCheck &amp;&amp; productTotal(product) &lt;= 0"><a class="btn cart-btn" @click="addToCart()"> <i class="bi bi-cart"> </i><span> {{ $t('add_to_cart') }}</span></a></div><div class="btns" v-if="userDataCheck &amp;&amp; productTotal(product) &gt; 0"><a class="btn added"> <i class="bi bi-check"></i></a></div><div class="btns" v-if="!userData"><router-link class="btn cart-btn" to="/account"> <i class="bi bi-cart"> </i><span> {{ $t('add_to_cart') }}</span></router-link></div><hr><div class="categories"><p> <span>{{ $t("categories") }}: </span><router-link to="/products" rel="tag">{{ categoryTitle }}</router-link></p></div><div class="social-share"><p>{{ $t('share_this_product') }}</p><ul><li><ShareNetwork class="facebook" network="facebook" :url="pageUrl.href" :title="product.title" :description="product.description" quote="" hashtags=""><i class="bi bi-facebook"> </i></ShareNetwork></li><li><ShareNetwork class="twitter" network="twitter" :url="pageUrl.href" :title="product.title"><i class="bi bi-twitter"></i></ShareNetwork></li><li><ShareNetwork class="pinterest" network="Pinterest" :media="product.image" :title="product.title"><i class="bi bi-pinterest"></i></ShareNetwork></li><li><ShareNetwork class="whatsapp" network="whatsapp" :url="pageUrl.href" :media="product.image" :title="product.title"><i class="bi bi-whatsapp"></i></ShareNetwork></li></ul></div></div></div></div></div></div></div></section><!--single product--><!--single product details--><section class="single-product-tabs" v-if="!loading"><div class="container"><div class="content"><ul class="nav nav-pills" role="tablist"><li class="nav-item" role="presentation"><button class="nav-link active" id="single-1-tab" data-bs-toggle="pill" data-bs-target="#single-1" type="button" role="tab" aria-controls="single-1" aria-selected="true">{{ $t('description') }}</button></li><li class="nav-item" role="presentation"><button class="nav-link" id="single-2-tab" data-bs-toggle="pill" data-bs-target="#single-2" type="button" role="tab" aria-controls="single-2" aria-selected="false">{{ $t('reviews') }} ({{ reviewsData.count }})</button></li></ul><div class="tab-content"><div class="tab-pane fade show active" id="single-1" role="tabpanel" aria-labelledby="single-1-tab"> <div class="products-content"><div v-html="product.details"></div></div></div><div class="tab-pane fade" id="single-2" role="tabpanel" aria-labelledby="single-2-tab"> <div class="products-content"><div class="row"><div class="col-lg-6"><div class="reviews-form"><h3 class="subtitle">{{ $t('add_review') }}</h3><p class="hint">{{ $t('your_email_address_will_not_be_published') }}.</p><div class="product-rating"><div class="rating-field"><p>{{ $t('rating') }}: </p><vue3-star-ratings v-model="reviewFormData.rate" :step="0.5" :showControl="false" :starSize="18"></vue3-star-ratings></div><div class="invalid-feedback" v-if="reviewErrors.rate &amp;&amp; !sent" v-for="error in reviewErrors.rate">{{ error }}</div></div><form enctype="multipart/form-data" @submit.prevent="handleReviewForm"><div class="row"><div class="col-md-12 form-group"><textarea class="form-control" :placeholder="$t('review_message')" rows="7" name="review" v-model="reviewFormData.review"></textarea><div class="invalid-feedback" v-if="reviewErrors.review &amp;&amp; !sent" v-for="error in reviewErrors.review">{{ error }}</div></div></div><div class="btn-form"><button class="btn primary" type="submit" name="button" v-if="userData"> <span v-if="!sending">{{ $t('send_review') }}</span><span v-if="sending">{{ $t('sending') }}</span><div class="spinner-border text-light" role="status" v-if="sending"><span class="visually-hidden">Loading...</span></div></button><router-link class="btn primary" to="/account" v-if="!userData">{{ $t('login_to_review') }}</router-link></div></form></div></div><div class="col-lg-6"><div class="reviews-content"><div class="reviews-title" v-if="reviewsData.count !== 0"><h3>{{ $t('reviews') }} </h3><button class="btn" type="button" data-bs-toggle="modal" data-bs-target="#reviewsModal">{{ $t('all_reviews') }}</button></div><div class="nodata" v-else><p class="nodata-text"><strong>{{ $t('no_reviews') }}</strong></p></div><ul v-if="reviewsData.reviews !== undefined"><li class="review-item" v-for="review in reviewsData.reviews.slice(0, 3)" :key="review.id"><img src="@/assets/images/blank.png" alt="review author"><div class="review-body"><div class="product-rating"><span class="ratyli"></span></div><h5>{{ review.name }}</h5><span>{{ review.created_at }}</span><p>{{ review.review }}</p></div></li></ul></div></div></div></div></div></div><div class="modal reviews-modal fade" id="reviewsModal" tabindex="-1" aria-labelledby="reviewsModalLabel" aria-hidden="true"><div class="modal-dialog modal-lg"><div class="modal-content"><div class="modal-header"><h5 class="modal-title" id="reviewsModalLabel">{{ $t('all_reviews') }}</h5><button class="btn-close btn-close-white" type="button" data-bs-dismiss="modal" aria-label="Close"></button></div><div class="modal-body"><div class="reviews-content"><ul v-if="reviewsData.reviews !== undefined"><li class="review-item" v-for="review in reviewsData.reviews.slice(0, 3)" :key="review.id"><img src="@/assets/images/blank.png" alt="review author"><div class="review-body"><div class="product-rating"><span class="ratyli"></span></div><h5>{{ review.name }}</h5><span>{{ review.created_at }}</span><p>{{ review.review }}</p></div></li></ul></div></div></div></div></div></div></div></section><!--single product details-->