<!--if login done--><Toast :showIfValue="login_done" :message="$t('you_have_login')" :success="true"></Toast><!--if login error--><Toast :showIfValue="login_error &amp;&amp; !login_done &amp;&amp; !register_error" :message="$t('unauthorised_message')" :success="false"></Toast><!--if register done--><Toast :showIfValue="register_done" :message="$t('you_have_registered')" :success="true"></Toast><!--login and register--><section class="login-register"><div class="container"><div class="login-register-area"><div class="login-register-box"><div class="login-register-content"><nav><div class="nav nav-tabs" id="nav-tab" role="tablist"><button class="nav-link active" id="nav-login-tab" data-bs-toggle="tab" data-bs-target="#nav-login" type="button" role="tab" aria-controls="nav-login" aria-selected="true">{{ $t("login") }}</button><button class="nav-link" id="nav-register-tab" data-bs-toggle="tab" data-bs-target="#nav-register" type="button" role="tab" aria-controls="nav-register" aria-selected="false">{{ $t("register") }}</button></div></nav><div class="tab-content" id="nav-tabContent"><div class="tab-pane fade" id="nav-register" role="tabpanel" aria-labelledby="nav-register-tab"> <form class="g-3" enctype="multipart/form-data" @submit.prevent="handleRegister"><div class="row"><div class="col-lg-12"><div class="form-group input-group"> <i class="input-group-text bi bi-person"></i><input class="form-control" type="text" :placeholder="$t('name')" v-model="registerFormData.name"><div class="invalid-feedback" v-if="registerErrors.name &amp;&amp; !register_done" v-for="error in registerErrors.name">{{ error }}</div></div></div><div class="col-lg-12"><div class="form-group input-group"><i class="input-group-text bi bi-telephone"></i><input class="form-control" type="number" :placeholder="$t('phone')" v-model="registerFormData.phone"><div class="invalid-feedback" v-if="registerErrors.phone &amp;&amp; !register_done" v-for="error in registerErrors.phone">{{ error }}</div></div></div><div class="col-lg-12"><div class="form-group input-group"><i class="input-group-text bi bi-envelope"></i><input class="form-control" type="email" :placeholder="$t('email')" v-model="registerFormData.email"><div class="invalid-feedback" v-if="registerErrors.email &amp;&amp; !register_done" v-for="error in registerErrors.email">{{ error }}</div></div></div><div class="col-lg-12"><div class="form-group input-group"><i class="input-group-text bi bi-shield-lock"></i><input class="form-control" type="password" :placeholder="$t('password')" v-model="registerFormData.password"><div class="invalid-feedback" v-if="registerErrors.password &amp;&amp; !register_done" v-for="error in registerErrors.password">{{ error }}</div></div></div><div class="col-lg-12 text-center"><div class="form-group"><button class="btn btn-danger hvr-sweep-to-top" :disabled="sendingRegister == true" type="submit">{{ $t("register_now") }}<div class="spinner-border text-light" role="status" v-if="sendingRegister"><span class="visually-hidden">Loading...</span></div></button></div></div></div></form></div><div class="tab-pane fade show active" id="nav-login" role="tabpanel" aria-labelledby="nav-login-tab"> <form class="g-3" enctype="multipart/form-data" @submit.prevent="handleLogin"><div class="row"><div class="col-lg-12"><div class="form-group input-group"><i class="input-group-text bi bi-envelope"></i><input class="form-control" type="email" :placeholder="$t('email')" v-model="loginFormData.email"><div class="invalid-feedback" v-if="loginErrors.email &amp;&amp; !login_done" v-for="error in loginErrors.email">{{ error }}</div></div></div><div class="col-lg-12"><div class="form-group input-group"><i class="input-group-text bi bi-shield-lock"></i><input class="form-control" type="password" :placeholder="$t('password')" v-model="loginFormData.password"><div class="invalid-feedback" v-if="loginErrors.password &amp;&amp; !login_done" v-for="error in loginErrors.password">{{ error }}</div></div></div><div class="col-lg-12 text-center"><div class="form-group"><button class="btn btn-danger hvr-sweep-to-top" type="submit" :disabled="sendingLogin == true">{{ $t("login") }}<div class="spinner-border text-light" role="status" v-if="sendingLogin"><span class="visually-hidden">Loading...</span></div></button></div></div></div></form></div></div></div></div></div></div></section><!--login and register -->