<!--cart item--><tr v-if="product"><td><router-link class="product-name" :to="`/products/${product.id}`"><div class="image"><img class="img-fluid" alt="Natus" :src="product.image"></div><p>{{ description }}...</p></router-link></td><td nowrap="nowrap"> <strong class="me-1">{{ Number(product.price) }} </strong>{{ $t("currency") }}</td><td> <div class="quantity"><i class="decrease-val bi bi-dash-lg" @click="removeFromCart(product)"></i><input class="val" type="number" min="1" max="500" :value="product.quantity" disabled><i class="increase-val bi bi-plus-lg" @click="addToCart(product)"></i></div></td><td nowrap="nowrap"> <strong class="me-1">{{ itemCost.toFixed(1) }} </strong>{{ $t("currency") }}</td><td><button class="btn remove removeFromProfile" type="submit" @click="removeProduct(product)">×</button></td></tr><!--cart item-->