<!--if wishlist updated--><Toast :showIfValue="updated" :message="$t('wishlist_updated_successfully')" :success="true"></Toast><!--wishlist--><section class="wishlist"><div class="container"><div class="wishlist-content"><div class="table-responsive table-area"><div class="notfound-txt" v-if="wishlist.length == 0"><p class="oops">{{ $t("oops") }}</p><p class="notfound-text"><strong>{{ $t("no_data_found") }}</strong></p></div><table class="table table-hover" v-if="wishlistCheck &amp;&amp; wishlist.length &gt; 0"><div class="wishlist-spinner" v-if="removing"><div class="spinner-border text-dark" role="status"><span class="visually-hidden">Loading...</span></div></div><thead><tr><th>{{ $t("product") }}</th><th>{{ $t("adding_date") }}</th><th>{{ $t("controle") }}</th></tr></thead><tbody><tr v-for="product in wishlist" :key="product.id"><td> <router-link class="product-name" :to="`/products/${product.id}`"><div class="image"><img class="img-fluid" :alt="product.image_alt" :src="product.image"></div><p>{{ product.title }}</p></router-link></td><td nowrap="nowrap">{{ product.created_at }}</td><td><form enctype="multipart/form-data" @submit.prevent="removeFromWishlist"><input name="product_id" :value="product.id" type="hidden"><button class="btn remove removeFromProfile" type="submit">×</button></form></td></tr></tbody></table></div></div></div></section><!--wishlist-->