<!--toast part--><div class="position-fixed bottom-0 p-3 toast-part"><div class="toast align-items-center text-white border-0" :class="{'show': showIfValue}, success ? 'bg-success' : 'bg-danger'" role="alert" aria-live="assertive" aria-atomic="true"><div class="d-flex"><div class="toast-body">{{ message }}</div><button class="btn-close btn-close-white" type="button" data-bs-dismiss="toast" aria-label="Close"></button></div></div></div><!--toast part-->